.signup-logo {
  margin-top: 20px;
  width: 20%;
}
.signup-holder {
  width: 100%;
  height: 70vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow-y: auto;
}
.signup-header {
  color: white;
  margin: 8px;
}
.signup {
  background-color: hsla(218, 50%, 20%, 1.0);
  border-radius: 10px;
  width: 40vw;
  text-align: center;
}
input {
  padding: 0.5rem;
  font-size: 16px;
  width: 90%;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-top: 10px;
}

input:focus {
  border-color: #007eff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

input.error {
  border-color: red;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.175),
    0 0 0 5px rgba(255, 0, 0, 0.1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.175),
    0 0 0 5px rgba(255, 0, 0, 0.1);
}
.error-label {
  color: red;
}
label {
  font-weight: bold;
  display: block;
  color: red;
  margin-bottom: 0.5rem;
}

.input-feedback {
  color: red;
  margin-top: 0.25rem;
}

button {
  max-width: 150px;
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
          box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed !important;
}

button + button {
  margin-left: 0.5rem;
}

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font: small-caption Verdana, sans-serif;
  color: rgba(255, 100, 100, 0.4);
}
::-moz-placeholder {
  /* Firefox 19+ */
  font: small-caption Verdana, sans-serif;
  color: pink;
}
:-ms-input-placeholder {
  /* IE 10+ */
  font: small-caption Verdana, sans-serif;
  color: pink;
}
:-moz-placeholder {
  /* Firefox 18- */
  font: small-caption Verdana, sans-serif;
  color: pink;
}

button#submit {
  background-color: #4db4d1;
  color: white;
}



